/**
 * Layout component that queries for data
 * with Gatsby's StaticQuery component
 *
 * See: https://www.gatsbyjs.org/docs/static-query/
 */

import React from "react"
import { StaticQuery, graphql } from "gatsby"

import { Container, Row, Col, Jumbotron } from "react-bootstrap"
import "../styles/_layout.scss"

import Header from "./header"
import FooterContent from "./footer-content"
import HeroAbout from "./hero-about"
import SkillsInfo from "./skills-info"
import WorkInfo from "./work-info"
import ContactInfo from "./contact-info"

const blackJumbotron = {
  background: "#000",
  color: "#fff",
}
const styledJumbotronSkills = {
  background: "linear-gradient(to right, #f3f2f2, #ffffff)",
  color: "#000",
}
const styledJumbotronWork = {
  background: "linear-gradient(to top, #FF9A8B 0%, #FF6A88 80%)",
  color: "#fff",
}

const styledJumbotronContact = {
  background: "linear-gradient(to right, #f3f2f2, #ffffff)",
  color: "#000",
}
const Layout = () => (
  <StaticQuery
    query={graphql`
      query SiteTitleQuery {
        site {
          siteMetadata {
            title
          }
        }
      }
    `}
    render={data => (
      <>
        <Row noGutters className="justify-content-center">
          <Col>
            <Header siteTitle={data.site.siteMetadata.title} />
          </Col>
        </Row>

        <Row noGutters style={blackJumbotron}>
          <Col>
            <Jumbotron className="hero-jumbotron" fluid style={blackJumbotron}>
              <Container>
                <HeroAbout />
              </Container>
            </Jumbotron>
          </Col>
        </Row>

        <Row noGutters style={styledJumbotronSkills}>
          <Col>
            <Jumbotron
              className="hero-jumbotron"
              fluid
              style={styledJumbotronSkills}
            >
              <Container>
                <SkillsInfo />
              </Container>
            </Jumbotron>
          </Col>
        </Row>

        <Row noGutters style={styledJumbotronWork}>
          <Col>
            <Jumbotron
              className="hero-jumbotron"
              fluid
              style={styledJumbotronWork}
            >
              <Container>
                <WorkInfo />
              </Container>
            </Jumbotron>
          </Col>
        </Row>

        <Row noGutters style={styledJumbotronContact}>
          <Col>
            <Jumbotron
              className="hero-jumbotron"
              fluid
              style={styledJumbotronContact}
            >
              <Container>
                <ContactInfo />
              </Container>
            </Jumbotron>
          </Col>
        </Row>

        <Row noGutters style={{ background: `#000`, color: `#fff` }}>
          <Col className="footer-col">
            <Jumbotron
              className="l"
              fluid
              style={{ background: `#000`, color: `#fff` }}
            >
              <FooterContent />
            </Jumbotron>
          </Col>
        </Row>
      </>
    )}
  />
)

export default Layout
