import { Row, Col, Container, Card, Button } from "react-bootstrap"
import React, { Component } from "react"
import "../styles/style.scss"
import WorkImageOne from "./work-image-one"
import WorkImageTwo from "./work-image-two"
import WorkImageThree from "./work-image-three"
import CurrentWork from "../images/revtap.inline.svg"
import Medium from "../images/medium.inline.svg"


const cardFooter = {
  background: `#22272A`,
  color: `#fff`,
}
const buttonStyle = {
  margin: `0.25rem`,
  background: `#22272A`,
  border: `none`,
  fontSize: `2rem`,
  color: `#fff`,
}

class WorkInfo extends Component {
  state = {
    loading: true,
    fetchedData: [],
  }

  componentDidMount() {
    /* 
    Async function to fetch data from the github API
    */
    async function getRepos() {
      try {
        let response = await fetch(
          "https://api.github.com/users/ingeniousambivert/repos?per_page=100&sort=created:desc&client_id=22ea12584240e7a2631f&client_secret=bc2deb37cb23d714f3b16483ba93a4a0e61f93e9"
        )
        let data = await response.json()
        return data
      } catch (error) {
        console.error(error)
      }
    }

    getRepos().then(data =>
      this.setState({
        fetchedData: data,
        loading: false,
      })
    )
  }

  render() {
    const { fetchedData, loading } = this.state
    return (
      <Container className="text-center font-raleway white-text">
        <Row id="work">
          <Col className="m-5">
            <h1
              className="minimal-text-shadow white-text"
              style={{
                fontWeight: "400",
                fontFamily: "Anton, sans-serif",
                fontSize: "4.5rem",
              }}
            >
              All that I've worked on
            </h1>
            <h6 className="minimal-text-shadow white-text">
              Some of my recent work
            </h6>
          </Col>
        </Row>
        <Row>
          <Col sm>
            <Container fluid id="gitOutput">
              {/* Used mapping , inline conditions and anonymous function to iterate and sort data */}
              {loading ? (
                <Button className="Cards" style={buttonStyle}>
                  Loading data ...
                </Button>
              ) : (
                fetchedData.map(repo =>
                  (() => {
                    if (
                      !repo.fork &&
                      repo.name !== "ingeniousambivert.github.io" && repo.name !== "ingeniousambivert"
                    ) {
                      return (
                        <Button
                          key={repo.name}
                          className="Cards"
                          style={buttonStyle}
                        >
                          <a
                           
                            href={repo.html_url}
                            rel="noopener noreferrer"
                            target="_blank"
                          >
                            {repo.name}
                          </a>
                        </Button>
                      )
                    }
                  })()
                )
              )}
            </Container>
            <Button
              className="m-3 Cards"
              style={{
                margin: `0.25rem`,
                background: `#fff`,
                border: `none`,
                fontSize: `1.25rem`,
              }}
            >
              <a
                style={{ color: `#000`,}}
                href="https://github.com/ingeniousambivert?tab=repositories"
                rel="noopener noreferrer"
                target="_blank"
              >
                view more
              </a>
            </Button>
          </Col>
        </Row>
        <div style={{ margin: `2rem` }}>
          <h6 className="minimal-text-shadow white-text">
            Some of my recent projects
          </h6>
        </div>
        <Row className="row row-cols-1 row-cols-md-3">
          <Col className="mb-3 ">
            <Card className="workCards Cards">
              <WorkImageOne />
              <Card.Footer style={cardFooter}>
                <a
                  href="https://chive.000webhostapp.com/"
                  rel="noopener noreferrer"
                  target="_blank"
                  style={{ color: `#fff` }}
                >
                  codersHive
                </a>
              </Card.Footer>
            </Card>
          </Col>

          <Col className="mb-3">
            <Card className="Cards">
              <WorkImageTwo className="workCards" />
              <Card.Footer style={cardFooter}>
                <a
                  href="https://swich.netlify.app/"
                  rel="noopener noreferrer"
                  target="_blank"
                  style={{ color: `#fff` }}
                >
                  Swich Developers
                </a>
              </Card.Footer>
            </Card>
          </Col>

          <Col className="mb-3">
            <Card id="au-cmsCard" className="Cards">
              <WorkImageThree className="workCards" />
              <Card.Footer style={cardFooter}>
                <a
                  href="https://aucms.herokuapp.com/"
                  rel="noopener noreferrer"
                  target="_blank"
                  style={{ color: `#fff` }}
                >
                  AU-Event Manager
                </a>
              </Card.Footer>
            </Card>
          </Col>
        </Row>
        <hr/>
        <Container fluid>
          <Row className="mt-5">
            <Col>
                <h5 className="minimal-text-shadow white-text">
                   I write some times, on &nbsp;
                          <a
                            href="https://ingeniousambivert.medium.com/"
                            rel="noopener noreferrer"
                            target="_blank">
                            <Medium style={{width: `7rem`, height: `1.75rem`}}/> 
                          </a>&nbsp;
                          and <br/> I am currently working for&nbsp;<a href="https://revtap.ai/"
                  target="_blank"
                  rel="noopener noreferrer">
                    <CurrentWork style={{width: `7rem`, height: `1.75rem`}}/>
                </a>
                  </h5>
            </Col>
          </Row>
        </Container>
      </Container>
    )
  }
}
export default WorkInfo
