import React from "react"
import { useStaticQuery, graphql } from "gatsby"
import Img from "gatsby-image"
import "../styles/style.scss"

export default function WorkImageOne() {
  const data = useStaticQuery(graphql`
    query MyWorkQueryOne {
      file(relativePath: { eq: "chive.webp" }) {
        id
        childImageSharp {
          fluid(maxWidth: 500) {
            ...GatsbyImageSharpFluid
          }
        }
      }
    }
  `)
  return (
    <div>
      <Img fluid={data.file.childImageSharp.fluid} alt="codersHive" />
    </div>
  )
}
