import React, { Component } from "react"
import { AiFillHeart } from "react-icons/ai"
import { Row, Col, Container } from "react-bootstrap"

import randomQ from "../utils/scripts"
import "../styles/_layout.scss"
import "../styles/style.scss"

export default class FooterContent extends Component {
  state = {
    loading: true,
    quote: "",
  }
  componentDidMount() {
    async function getQuotes() {
      try {
        let response = await randomQ()
        let data = await response.toString()
        return data
      } catch (error) {
        console.error(error)
      }
    }

    getQuotes().then(data =>
      this.setState({
        quote: data,
        loading: false,
      })
    )
  }
  render() {
    const { quote, loading } = this.state
    return (
      <Container
        className="font-raleway "
        style={{ background: `#000`, color: `#fff` }}
      >
        <Row>
          <Col className="text-left" xs={12} md={8}>
            <h2 className="white-text">Inspiration</h2>
            <blockquote id="quote">
              {loading ? <p>Loading quote ...</p> : quote}
            </blockquote>
          </Col>
          <Col xs={6} md={4} className="mt-4">
            <a
              style={{ color: `#fff` }}
              href={`mailto:monarchmaisuriya7600@gmail.com`}
            >
              monarchmaisuriya7600@gmail.com
            </a>

            <h5>
              Made with <AiFillHeart />
            </h5>
          </Col>
        </Row>
      </Container>
    )
  }
}
